import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  rooms: [],
  error: false,
  loading: false,
};

const rooms = createSlice({
  name: "rooms",
  initialState,
  reducers: {
    setRooms(state, action) {
      state.rooms = action.payload;
    },
  },
});

export const { setRooms } = rooms.actions;

export default rooms.reducer;
