import { useRef, useState } from "react";
import Dialog from "../dialog/dialog";
import "./rooms-page.css";
import { useSelector } from "react-redux";
import { createRoom, joinRoom } from "../services/socket";

const RoomPage = () => {
  const ref = useRef(null);

  const rooms = useSelector(state => state.rooms.rooms);
  const [roomName, setRoomName] = useState("");
  const [selectedRoom, setSelectedRoom] = useState("");

  const openModal = () => {
    setRoomName("");
    ref.current.showModal();
  };

  const onSubmit = e => {
    e.preventDefault();
    ref.current.close();
    createRoom(roomName);
  };

  const items = rooms.map(({ name, id, users }) => {
    let className = "room-item";
    if (id === selectedRoom) {
      className += " selected";
    }
    return (
      <li key={id} onClick={() => setSelectedRoom(id)} className={className}>
        {name} ({users.length})
      </li>
    );
  });
  return (
    <>
      <div className="rooms-page form-page">
        <p> </p>
        <ul className="room-list">
          {items.length ? items : "  "}
        </ul>
        <div className="form-page-buttons">
          <button
            disabled={!selectedRoom}
            onClick={() => joinRoom(selectedRoom)}
          >
            
          </button>
          <button onClick={openModal}></button>
        </div>
      </div>
      <Dialog ref={ref}>
        <div className="form-page modal-form-page">
          <p> </p>
          <form
            style={{ display: "flex", columnGap: "2px" }}
            onSubmit={onSubmit}
          >
            <input
              value={roomName}
              onChange={e => setRoomName(e.target.value)}
              placeholder=" "
            />
            <button></button>
          </form>
        </div>
      </Dialog>
    </>
  );
};

export default RoomPage;
