import { Layer, Stage } from "react-konva";
import Menu from "../menu/menu";
import React, { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import Dialog from "../dialog/dialog";
import { check } from "../services/socket";
import Tile from "../tile/tile";

const strokeWidth = 1;
const hexMargin = 0;

const Map = () => {
  const ref = useRef(null);
  const modalRef = useRef(null);

  const { login } = useSelector(state => state.profile);
  const {
    game: { checkIdx, searchIdx, users, map, stage, actionLogin, lastUpdate },
    hoverIdx,
    hoverPlayerColor,
    hoverBuilding,
    hoverAnimal,
    hoverAnswer,
  } = useSelector(state => state.game);

  const playerColor = users.find(u => u.login === login).color;
  const [state, setState] = useState(map);
  const [anchor, setAnchor] = useState(null);
  const [{ width, height }, setCanvasSize] = useState({
    width: 1,
    height: 1,
  });

  const radius = Math.abs(Math.min(width / 19, height / 16.5)) - strokeWidth;

  const horizontalHexOffset = radius * Math.sin(Math.PI / 6);
  const verticalHexOffset = radius * Math.cos(Math.PI / 6);

  const fullWidth = radius * 17;
  const startX = (width - fullWidth) / 2;
  const startY = 7 + radius;

  const resizeCanvas = () => {
    if (ref.current?.offsetHeight && ref.current?.offsetWidth) {
      setCanvasSize({
        width: ref.current.offsetWidth,
        height: ref.current.offsetHeight,
      });
    }
  };

  useEffect(() => {
    setState(map);
  }, [map]);

  useEffect(() => {
    window.addEventListener("resize", resizeCanvas);
    return () => {
      window.removeEventListener("resize", resizeCanvas);
    };
  }, []);

  useEffect(() => {
    resizeCanvas();
  }, []);

  const openModal = () => {
    modalRef.current.showModal();
  };

  const setHover = (i, value) => {
    setState(oldState => {
      const newState = oldState.map(value => {
        return { ...value };
      });
      newState[i].hover = value;
      return newState;
    });
  };

  const askPlayer = login => {
    check({ idx: anchor.i, login });
    modalRef.current.close();
    setAnchor(null);
  };
  const dialogUsers = users.map(u => {
    if (u.login === login) return null;

    return (
      <li
        onClick={() => askPlayer(u.login)}
        className={`list-item player-${u.color}`}
        key={u.login + "selectlogin"}
      >
        {u.login}
      </li>
    );
  });

  const handleClick = e => {
    if (e.target?.attrs?.className !== "tile") return setAnchor(null);
  };
  const itemsJsx = [];
  for (let i = 0; i < state.length; i++) {
    const openMenu = input => {
      if (login === actionLogin) {
        if (stage === "check" && checkIdx && checkIdx !== i) return;
        if (stage === "search" && searchIdx && searchIdx !== i) return;
        setAnchor(input);
      }
    };

    itemsJsx.push(
      <Tile
        key={i + "tile"}
        item={state[i]}
        i={i}
        hoverIdx={hoverIdx}
        rad={radius}
        horizontalHexOffset={horizontalHexOffset}
        verticalHexOffset={verticalHexOffset}
        startX={startX}
        startY={startY}
        stage={stage}
        openMenu={openMenu}
        lastUpdate={lastUpdate}
        checkIdx={checkIdx}
        searchIdx={searchIdx}
        setHover={setHover}
        hexMargin={hexMargin}
        sw={strokeWidth}
        hoverPlayerColor={hoverPlayerColor}
        hoverBuilding={hoverBuilding}
        hoverAnimal={hoverAnimal}
        hoverAnswer={hoverAnswer}
      />
    );
  }
  return (
    <div ref={ref} className="map-wrapper">
      <Stage onClick={handleClick} width={width} height={height}>
        <Layer>
          {itemsJsx}
          <Menu
            checkIdx={checkIdx}
            openModal={openModal}
            anchor={anchor}
            stage={stage}
            item={state[anchor?.i]}
            close={() => setAnchor(null)}
            playerColor={playerColor}
            searchIdx={searchIdx}
          />
        </Layer>
      </Stage>
      <Dialog ref={modalRef}>
        <div className="ask-modal">
          <p> :</p>
          <ul className="user-list list">{dialogUsers}</ul>
        </div>
      </Dialog>
    </div>
  );
};

export default Map;
