import { useSelector } from "react-redux";
import LoginPage from "../login/loginPage";
import RoomsPage from "../roomsPage/roomsPage";
import RoomPage from "../roomPage/roomPage";
import Game from "../game/game";

const PageManager = () => {
  const connectInfo = useSelector(state => state.connectInfo);
  const profile = useSelector(state => state.profile);
  const { game } = useSelector(state => state.game);

  if (!connectInfo.connected) return <div>Loading...</div>;
  if (!profile.login) return <LoginPage />;
  if (!profile.room) return <RoomsPage />;
  if (!game) return <RoomPage />;
  return <Game />;
};

export default PageManager;
