import { Html } from "react-konva-utils";
import { cantBe, search } from "../services/socket";

const Menu = ({
  anchor,
  stage,
  close,
  item,
  openModal,
  checkIdx,
  searchIdx,
  playerColor,
}) => {
  if (!anchor) return null;

  const onCantBe =
    (value = false) =>
    () => {
      close();
      cantBe(anchor.i, value);
    };

  const onSearch = () => {
    close();
    search(anchor.i);
  };

  return (
    <Html
      divProps={{
        className: "action-menu",
        style: {
          top: `${anchor.y}px`,
          left: `${anchor.x}px`,
        },
      }}
    >
      {(stage === "start" || stage === "check" || stage === "search") && (
        <button disabled={item.answers.cubes.length} onClick={onCantBe()}>
           
        </button>
      )}
      {stage === "turn" && (
        <>
          <button disabled={item.answers.cubes.length} onClick={openModal}>
             
          </button>
          <button onClick={onSearch} disabled={item.answers.cubes.length}>
            
          </button>
        </>
      )}
      {((stage === "check" && checkIdx) ||
        (stage === "search" && searchIdx) ||
        stage === "canbe") && (
        <button
          disabled={item.answers.circles.includes(playerColor)}
          onClick={onCantBe(true)}
        >
          
        </button>
      )}
    </Html>
  );
};

export default Menu;
