import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  connected: false,
};

const connect = createSlice({
  name: "connectInfo",
  initialState,
  reducers: {
    setConnect(state) {
      state.connected = true;
    },
  },
});

export const { setConnect } = connect.actions;

export default connect.reducer;
