import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  game: null,
  hoverIdx: null,
  hoverPlayerColor: null,
  hoverBuilding: null,
  hoverAnimal: null,
  hoverAnswer: null,
};

const game = createSlice({
  name: "game",
  initialState,
  reducers: {
    setGame(state, action) {
      state.game = action.payload;
    },
    setHover(state, action) {
      state.hoverIdx = action.payload;
    },
    setHoverPlayerColor(state, action) {
      state.hoverPlayerColor = action.payload;
    },
    setHoverBuilding(state, action) {
      state.hoverBuilding = action.payload;
    },
    setHoverAnimal(state, action) {
      state.hoverAnimal = action.payload;
    },
    setHoverAnswer(state, action) {
      state.hoverAnswer = action.payload;
    },
  },
});

export const {
  setGame,
  setHover,
  setHoverPlayerColor,
  setHoverBuilding,
  setHoverAnimal,
  setHoverAnswer,
} = game.actions;

export default game.reducer;
