import React from "react";
import { useDispatch } from "react-redux";
import { setHover } from "../store/slices/game-slice";
import "./log.css";

const LogItem = ({ item }) => {
  const { action, idx, user, secondUser } = item;
  const dispatch = useDispatch();
  let state = null;
  if (action === "cube" || action === "circle") {
    state = (
      <>
        <span className={`player-${user.color}`} style={{ fontWeight: 700 }}>
          {user.login}
        </span>
        
        <span
          className={`log-answer ${action}`}
          style={{ background: `var(--player-${user.color})` }}
        ></span>
      </>
    );
  }
  if (action === "check") {
    state = (
      <>
        <span className={`player-${user.color}`} style={{ fontWeight: 700 }}>
          {user.login}
        </span>
         
        <span
          className={`player-${secondUser.color}`}
          style={{ fontWeight: 700 }}
        >
          {secondUser.login}
        </span>
      </>
    );
  }
  if (action === "search") {
    state = (
      <>
        <span className={`player-${user.color}`} style={{ fontWeight: 700 }}>
          {user.login}
        </span>
         
      </>
    );
  }
  return (
    <div
      onMouseLeave={() => dispatch(setHover(null))}
      onMouseEnter={() => dispatch(setHover(idx))}
      className="log-item"
    >
      {state}
    </div>
  );
};

export default LogItem;
