import { useSelector } from "react-redux";
import { startGame } from "../services/socket";

const RoomPage = () => {
  const rooms = useSelector(state => state.rooms.rooms);
  const profile = useSelector(state => state.profile);
  const roomId = profile.room;
  const currentRoom = rooms.find(r => r.id === roomId);
  const users = currentRoom?.users || [];
  const items = users.map(login => {
    return (
      <li key={login} className="list-item">
        {login}
      </li>
    );
  });
  return (
    <div className="room-page form-page">
      <p>: {currentRoom?.name}</p>
      <ul className="user-list list">{items.length ? items : " "}</ul>
      <div className="form-page-buttons">
        <button onClick={() => startGame(roomId)}></button>
      </div>
    </div>
  );
};

export default RoomPage;
