import Lottie from "lottie-react";
import React, { useEffect, useRef } from "react";
import lottieRefresh from "../lottie/lottieRefresh.json";

const RefreshIcon = () => {
  const ref = useRef(null);

  useEffect(() => {
    if (ref.current) {
      ref.current.setSpeed(2);
    }
  }, [ref]);

  const onMouseEnter = () => {
    ref.current.setDirection(1);
    ref.current.play();
  };

  const onMouseLeave = () => {
    ref.current.setDirection(-1);
    ref.current.play();
  };

  return (
    <Lottie
      lottieRef={ref}
      onMouseEnter={onMouseEnter}
      animationData={lottieRefresh}
      loop={false}
      autoplay={false}
      onMouseLeave={onMouseLeave}
    />
  );
};

export default RefreshIcon;
