import React, { useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import LogItem from "../logItem/logItem";
import "./notepad.css";

const Notepad = () => {
  const log = useSelector(state => state.game.game.log);
  const ref = useRef();

  useEffect(() => {
    ref.current.scrollTo(0, ref.current.scrollHeight);
  }, [log]);
  const items = log.map(item => <LogItem key={item.id} item={item} />);

  return (
    <div className="right-wrapper">
      <div className="block-wrapper">
        <h1>:</h1>
        <textarea spellCheck={false} className="notepad" />
      </div>
      <div className="block-wrapper log-wrapper">
        <h1>:</h1>
        <div ref={ref} className="log">
          {items}
        </div>
      </div>
    </div>
  );
};

export default Notepad;
