import React from "react";
import "./bioms.css";
import { useDispatch } from "react-redux";
import { setHoverAnimal } from "../store/slices/game-slice";

const Bioms = () => {
  const dispatch = useDispatch();
  return (
    <div className="block-wrapper bioms-wrapper">
      <h1>:</h1>
      <ul className="legen-list">
        <li className="legend-item">
          <div className="legend-image forest" />
          <div className="legend-text"></div>
        </li>
        <li className="legend-item">
          <div className="legend-image water" />
          <div className="legend-text"></div>
        </li>
        <li className="legend-item">
          <div className="legend-image swamp" />
          <div className="legend-text"></div>
        </li>
        <li className="legend-item">
          <div className="legend-image desert" />
          <div className="legend-text"></div>
        </li>
        <li className="legend-item">
          <div className="legend-image rock" />
          <div className="legend-text"></div>
        </li>
        <li
          onMouseEnter={() => dispatch(setHoverAnimal("jaguar"))}
          onMouseLeave={() => dispatch(setHoverAnimal(null))}
          className="legend-item"
        >
          <div className="legend-image jaguar" />
          <div className="legend-text"></div>
        </li>
        <li
          onMouseEnter={() => dispatch(setHoverAnimal("bear"))}
          onMouseLeave={() => dispatch(setHoverAnimal(null))}
          className="legend-item"
        >
          <div className="legend-image bear" />
          <div className="legend-text"></div>
        </li>
      </ul>
    </div>
  );
};

export default Bioms;
