import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { login } from "../services/socket";
import "./login-page.css";

const LoginPage = () => {
  const profile = useSelector(state => state.profile);
  const [nickname, setNickname] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    const nickname = localStorage.getItem("login");
    if (nickname) login({ nickname, initial: true });
  }, []);

  const onSubmit = e => {
    e.preventDefault();
    if (!nickname) return setError("  ");
    login({ nickname });
  };

  return (
    <form onSubmit={onSubmit} className="login-page form-page">
      <label htmlFor="login">:</label>
      <input
        placeholder=" "
        value={nickname}
        onChange={e => setNickname(e.target.value)}
        id="login"
        type="text"
      />

      <div>
        <button></button>
      </div>

      {!!(profile.error || error) && (
        <div className="auth-page-error">{error || profile.error}</div>
      )}
    </form>
  );
};

export default LoginPage;
