import React from "react";
import { RegularPolygon, Arc, Star, Group } from "react-konva";
import { getColor } from "../colors/colors";

const columns = 6;

const Tile = ({
  item,
  i,
  rad,
  horizontalHexOffset,
  verticalHexOffset,
  startX,
  startY,
  stage,
  openMenu,
  lastUpdate,
  checkIdx,
  searchIdx,
  setHover,
  hexMargin,
  sw,
  hoverIdx,
  hoverPlayerColor,
  hoverBuilding,
  hoverAnimal,
  hoverAnswer,
}) => {
  const {
    type,
    hover,
    special,
    building,
    answers: { cubes, circles },
  } = item;
  const row = Math.floor(i / columns);
  const col = i % columns;
  const withOffset = row % 2 === 1;
  let radius = Math.max(rad, 0);
  let strokeWidth = sw;

  let x = startX + col * radius * 3;
  if (!withOffset) x += col * 2 * hexMargin;
  if (withOffset) x += hexMargin + col * 2 * hexMargin;

  if (withOffset) x += horizontalHexOffset + radius;
  const y = startY + row * verticalHexOffset + row * (hexMargin / 2);

  const fillColor = getColor(type);

  let opacity = 1;
  if (hover) opacity = 0.5;
  if (stage === "check" && checkIdx && checkIdx !== i) {
    opacity = 0.2;
  }
  if (stage === "search" && searchIdx && searchIdx !== i) {
    opacity = 0.2;
  }

  if (hoverIdx && i !== hoverIdx) {
    opacity = 0.2;
  }
  if (
    hoverPlayerColor &&
    !cubes.includes(hoverPlayerColor) &&
    !circles.includes(hoverPlayerColor)
  ) {
    opacity = 0.2;
  }

  if (hoverBuilding && building?.type !== hoverBuilding) {
    opacity = 0.2;
  }
  if (hoverAnimal && special !== hoverAnimal) {
    opacity = 0.2;
  }
  if (
    hoverAnswer?.answer === "circle" &&
    !circles.includes(hoverAnswer.color)
  ) {
    opacity = 0.2;
  }
  if (hoverAnswer?.answer === "cube" && !cubes.includes(hoverAnswer.color)) {
    opacity = 0.2;
  }

  const angle = circles.length ? 360 / circles.length : 0;

  const scale = lastUpdate === i ? 0.75 : 1;

  const buildingY = y + radius / 2;
  return (
    <React.Fragment>
      <Group>
        <RegularPolygon
          className="tile"
          sides={6}
          x={x}
          y={y}
          radius={radius}
          fill={fillColor}
          stroke="#363237"
          strokeWidth={strokeWidth}
          rotation={30}
          opacity={opacity}
          onClick={() => openMenu({ x, y, i })}
          onMouseEnter={() => setHover(i, true)}
          onMouseLeave={() => setHover(i, false)}
          scaleX={scale}
          scaleY={scale}
        />
        {special ? (
          <RegularPolygon
            className="tile"
            sides={6}
            x={x}
            y={y}
            radius={Math.max(radius - 8, 0)}
            fill="transparent"
            stroke={getColor(special)}
            strokeWidth={4}
            rotation={30}
            opacity={opacity === 1 ? opacity : 0.5}
            onClick={() => openMenu({ x, y, i })}
            onMouseEnter={() => setHover(i, true)}
            onMouseLeave={() => setHover(i, false)}
            scaleX={scale}
            scaleY={scale}
          />
        ) : null}
        {building?.type === "hut" ? (
          <RegularPolygon
            className="tile"
            sides={3}
            x={x}
            y={buildingY}
            radius={radius / 4}
            fill={getColor(`building-${building.color}`)}
            strokeWidth={1}
            stroke="#363237"
          />
        ) : null}
        {building?.type === "monument" ? (
          <Star
            x={x}
            y={buildingY}
            numPoints={5}
            innerRadius={radius / 6}
            outerRadius={radius / 4}
            fill={getColor(`building-${building.color}`)}
            stroke="#363237"
            strokeWidth={1}
          />
        ) : null}

        {cubes[0] ? (
          <RegularPolygon
            className="tile"
            sides={4}
            x={x - radius / 2.7}
            y={y - radius / 2.7}
            radius={radius / 4}
            fill={getColor(`player-${cubes[0]}`)}
            strokeWidth={1}
            stroke="#363237"
            rotation={45}
            onClick={() => openMenu({ x, y, i })}
            onMouseEnter={() => setHover(i, true)}
            onMouseLeave={() => setHover(i, false)}
          />
        ) : null}
        {circles.length ? (
          <>
            {circles.map((item, idx) => {
              return (
                <Arc
                  key={i + "circle" + idx}
                  x={x + radius / 2.7}
                  y={y - radius / 2.7}
                  innerRadius={0}
                  outerRadius={radius / 4}
                  angle={angle}
                  strokeWidth={1}
                  stroke="#363237"
                  fill={getColor(`player-${item}`)}
                  rotation={angle * idx}
                />
              );
            })}
          </>
        ) : null}
      </Group>
    </React.Fragment>
  );
};

export default Tile;
