import { io } from "socket.io-client";
import store from "../store/store";
import { setHint, setLogin, setRoom } from "../store/slices/profile-slice";
import { setConnect } from "../store/slices/connect-slice";
import { setRooms } from "../store/slices/rooms-slice";
import { setGame } from "../store/slices/game-slice";
// localStorage.setItem("login", "");
const URL =
  process.env.NODE_ENV === "development"
    ? "ws://localhost:8888/cryptid"
    : "wss://general-server-vbl.amvera.io/cryptid";

const socket = io(URL);

socket.on("connect", () => {
  store.dispatch(setConnect());
});

socket.on("updateRooms", rooms => {
  store.dispatch(setRooms(rooms));
});
socket.on("setRoom", room => {
  store.dispatch(setRoom(room));
});

const startGameCallback = ({ game }) => {
  if (game) store.dispatch(setGame(game));
};

socket.on("startGame", data => {
  startGameCallback(data);
});
socket.on("hint", data => {
  store.dispatch(setHint(data));
});

socket.on("voice", text => {
  speechSynthesis.speak(new SpeechSynthesisUtterance(text));
});

const loginCallback = ({ profile, rooms }) => {
  if (!profile) return;
  if (profile.login) localStorage.setItem("login", profile.login);
  store.dispatch(setLogin(profile));
  store.dispatch(setRooms(rooms));
};

const login = ({ nickname, initial }) => {
  socket.emit("login", { login: nickname, initial }, loginCallback);
};

const createRoomCallback = ({ roomId }) => {
  if (roomId) store.dispatch(setRoom(roomId));
};

const createRoom = roomName => {
  socket.emit("createRoom", { roomName }, createRoomCallback);
};

const joinRoomCallback = roomId => {
  if (roomId) store.dispatch(setRoom(roomId));
};
const exitCallback = () => {
  store.dispatch(setGame(null));
  store.dispatch(setRoom(null));
};
const joinRoom = roomId => {
  socket.emit("joinRoom", { roomId }, joinRoomCallback);
};

const startGame = roomId => {
  socket.emit("startGame", { roomId }, startGameCallback);
};

const cantBe = (idx, canBe) => {
  socket.emit("cantBe", { idx, canBe });
};

const check = ({ idx, login }) => {
  socket.emit("check", { idx, login });
};

const search = idx => {
  socket.emit("search", { idx });
};
const refresh = roomId => {
  socket.emit("refresh", { roomId });
};

const exit = () => {
  socket.emit("exit", {}, exitCallback);
};
const prevStep = () => {
  socket.emit("prevStep");
};
const sendVoice = text => {
  socket.emit("voice", text);
};

export {
  login,
  createRoom,
  joinRoom,
  startGame,
  cantBe,
  check,
  search,
  refresh,
  exit,
  prevStep,
  sendVoice,
};
export default socket;
