import React, { useEffect, useRef } from "react";
import Map from "../map/map";
import { useSelector } from "react-redux";
import PlayersInfo from "../playersInfo/playersInfo";
import NotifSound from "../audio/not.mp3";
import "./game.css";
import GameInfo from "../gameInfo/gameInfo";
import Hint from "../hint/hint";
import Notepad from "../notepad/notepad";
import Bioms from "../bioms/bioms";
import Legend from "../legend/legend";
import Message from "../message/message";

const Game = () => {
  const ref = useRef();
  const {
    game: { actionLogin, stage },
  } = useSelector(state => state.game);
  const { login } = useSelector(state => state.profile);

  useEffect(() => {
    if (ref?.current && login === actionLogin) ref.current?.play();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [actionLogin, ref, stage]);

  return (
    <div className="game-page">
      <audio ref={ref} src={NotifSound} />
      <PlayersInfo />
      <GameInfo />
      <Map />
      <Hint />
      <Notepad />
      <Bioms />
      <Legend />
      <Message />
    </div>
  );
};

export default Game;
