import React, { useRef } from "react";
import { useSelector } from "react-redux";
import { exit, prevStep, refresh } from "../services/socket";
import RefreshIcon from "../refreshIcon/refreshIcon";
import "./game-info.css";
import ThemeSwitch from "../themeSwitch/themeSwitch";
import ExitIcon from "../exitIcon/exitIcon";
import Dialog from "../dialog/dialog";
import BackIcon from "../backIcon/backIcon";
import HelpIcon from "../helpIcon/helpIcon";

const GameInfo = () => {
  const ref = useRef(null);
  const tipsRef = useRef(null);
  const game = useSelector(state => state.game.game);
  const { actionLogin, stage, winner, roomId, users } = game;

  let text = null;
  const loginToSearch = winner || actionLogin;
  const color = users.find(u => u.login === loginToSearch)?.color;
  if (stage === "start") text = ` -  `;
  if (stage === "turn") text = ` -     `;
  if (stage === "search") text = ` - `;
  if (stage === "check") text = ` - `;
  if (stage === "canbe") text = ` -  `;
  if (winner) text = ` - `;

  return (
    <div className="game-info">
      <div className="block-wrapper">
        <h1>:</h1>
        <span>
          <span className={`nickname player-${color}`}>{loginToSearch}</span>
          {text}
        </span>
      </div>
      <div className="buttons">
        <ThemeSwitch />
        <span
          title=" "
          onClick={() => refresh(roomId)}
          className="icon"
        >
          <RefreshIcon />
        </span>

        <span
          className="icon"
          title=" "
          onClick={() => {
            prevStep();
          }}
        >
          <BackIcon />
        </span>
        <span
          title=" "
          className="icon"
          onClick={() => tipsRef.current.showModal()}
        >
          <HelpIcon />
        </span>
        <span
          title=""
          className="icon"
          onClick={() => ref.current.showModal()}
        >
          <ExitIcon />
        </span>
      </div>
      <Dialog ref={ref}>
        <div className="form-page modal-form-page">
          <p>  ?</p>
          <div style={{ display: "flex", justifyContent: "space-around" }}>
            <button onClick={() => exit()}></button>
            <button onClick={() => ref.current.close()}></button>
          </div>
        </div>
      </Dialog>
      <Dialog ref={tipsRef}>
        <div className="tips-modal">
          <h1> </h1>
          <h2>    :</h2>
          <div className="tips-wrapper">
            <ul>
              <li>    </li>
              <li>    </li>
              <li>    </li>
              <li>    </li>
              <li>    </li>
            </ul>
            <ul>
              <li>    </li>
              <li>    </li>
              <li>    </li>
              <li>    </li>
              <li>    </li>
            </ul>
          </div>
          <h2>        :</h2>
          <div className="tips-wrapper">
            <ul>
              <li>     </li>
              <li>     </li>
              <li>     </li>
            </ul>
            <ul>
              <li>     </li>
              <li>     </li>
              <li>      </li>
            </ul>
          </div>
          <h2>        :</h2>
          <div className="tips-wrapper">
            <ul>
              <li>     </li>
              <li>     </li>
            </ul>
            <ul>
              <li>     </li>
              <li>     </li>
            </ul>
          </div>
          <h2>       :</h2>
          <div className="tips-wrapper">
            <ul>
              <li>      </li>
              <li>      </li>
            </ul>
            <ul>
              <li>      </li>
              <li>      </li>
            </ul>
          </div>
        </div>
      </Dialog>
    </div>
  );
};

export default GameInfo;
