import React, { useRef } from "react";
import { useSelector } from "react-redux";
import Dialog from "../dialog/dialog";
import blue from "../images/blue.jpg";
import red from "../images/red.jpg";
import purple from "../images/purple.jpg";
import brown from "../images/brown.jpg";
import green from "../images/green.jpg";
import "./hint.css";
const Hint = () => {
  const { hint: hintObj } = useSelector(state => state.profile);
  const { hint, hintIdx, hintNumber } = hintObj || {};
  const ref = useRef();
  return (
    <div className="block-wrapper hint-wrapper">
      <h1>:</h1>
      <div className="hint" onClick={() => ref.current.showModal()}>
        {hintNumber}: {hint}
      </div>
      <Dialog ref={ref}>
        <div>
          {hintIdx === 0 && <img src={red} alt="red" />}
          {hintIdx === 1 && <img src={green} alt="green" />}
          {hintIdx === 2 && <img src={blue} alt="blue" />}
          {hintIdx === 3 && <img src={brown} alt="brown" />}
          {hintIdx === 4 && <img src={purple} alt="purple" />}
        </div>
      </Dialog>
    </div>
  );
};

export default Hint;
