import Lottie from "lottie-react";
import React, { useRef } from "react";
import lottieDoor from "../lottie/lottieHelp.json";

const HelpIcon = () => {
  const ref = useRef(null);

  const onMouseEnter = () => {
    ref.current.setDirection(1);
    ref.current.play();
  };

  const onMouseLeave = () => {
    ref.current.setDirection(-1);
    ref.current.play();
  };

  return (
    <Lottie
      lottieRef={ref}
      onMouseEnter={onMouseEnter}
      animationData={lottieDoor}
      loop={false}
      autoplay={false}
      onMouseLeave={onMouseLeave}
    />
  );
};

export default HelpIcon;
