import React from "react";
import "./legend.css";
import { useDispatch } from "react-redux";
import { setHoverBuilding } from "../store/slices/game-slice";

const Legend = () => {
  const dispatch = useDispatch();
  return (
    <div className="block-wrapper legend-wrapper">
      <h1>:</h1>
      <ul className="legen-list" style={{ justifyContent: "space-around" }}>
        <li
          onMouseEnter={() => dispatch(setHoverBuilding("monument"))}
          onMouseLeave={() => dispatch(setHoverBuilding(null))}
          className="legend-item"
        >
          <div className="legend-image star-legend" />
          <div className="legend-text"></div>
        </li>
        <li
          onMouseEnter={() => dispatch(setHoverBuilding("hut"))}
          onMouseLeave={() => dispatch(setHoverBuilding(null))}
          className="legend-item"
        >
          <div className="legend-image triangle" />
          <div className="legend-text"></div>
        </li>
      </ul>
    </div>
  );
};

export default Legend;
