import Lottie from "lottie-react";
import React, { useRef } from "react";
import lottieDoor from "../lottie/lottieDoor.json";

const ExitIcon = () => {
  const ref = useRef(null);

  const onMouseEnter = () => {
    ref.current.setDirection(1);
    ref.current.play();
  };

  const onMouseLeave = () => {
    ref.current.setDirection(-1);
    ref.current.play();
  };

  return (
    <Lottie
      lottieRef={ref}
      onMouseEnter={onMouseEnter}
      animationData={lottieDoor}
      loop={false}
      initialSegment={[18, 46]}
      autoplay={false}
      onMouseLeave={onMouseLeave}
    />
  );
};

export default ExitIcon;
