import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  dark: false,
};

const theme = createSlice({
  name: "theme",
  initialState,
  reducers: {
    setTheme(state, action) {
      state.dark = action.payload;
    },
  },
});

export const { setTheme } = theme.actions;

export default theme.reducer;
