import Lottie from "lottie-react";
import React, { useRef } from "react";
import lottieDoor from "../lottie/lottieBack.json";

const BackIcon = () => {
  const ref = useRef(null);

  const onMouseEnter = () => {
    ref.current.setDirection(1);
    ref.current.play();
  };

  const onMouseLeave = () => {
    ref.current.setDirection(-1);
    ref.current.play();
  };

  return (
    <Lottie
      lottieRef={ref}
      onMouseEnter={onMouseEnter}
      animationData={lottieDoor}
      loop={false}
      initialSegment={[1, 40]}
      autoplay={false}
      onMouseLeave={onMouseLeave}
    />
  );
};

export default BackIcon;
